<template>
  <div id="app">
    <!-- <b-button variant="info" class="blockButton"   @click ="exportPdf">
                Export PDF
              </b-button> -->
    <div class="container">   
      <b-navbar-nav class="mr-auto">       
        
    <b-button variant="info" @click="exportPdf">Save</b-button>
    </b-navbar-nav>
  </div>
  </div>
</template>


<script>
import jsPDF from "jspdf";
// import image2base64  from 'image-to-base64';

export default {
  name: "pdf",
  data() {
    return {
      // name: 'Nichamon'
      title: 'Order List',
      bar: '___________________________________________________________________',
      by:   'By:_____________________',
      date: 'Date:______/______/______',
      water: 'PinidaShop',
      customerH: [
          {
          nameH: 'Name        : ', addressH: 'Address    : ', telephoneH: 'Telephone: '
        }
      ],
      customer:[ {
          // place holder
          name: "ef", address: "dv", telephone: "cd"
      }],
      column: [
          {
          pattern: 'Pettern', color: 'Color', size: 'Size', amount: 'Amount'
        }
      ],
      name: [
        {
          pattern: 'B2', color: 'Blue', size: 'S', amount: '10'
        },
        {
          pattern: 'B1', color: 'Red', size: 'L', amount: '5'
        }
      ]
    }
  },
  methods: {
    data() {
      // let pdfName = this.$store.getters.collect.name;
      // let pdfAddress = this.$store.getters.detail.address;
      // let pdfPhone = this.$store.getters.detail.phone;

       /* eslint-disable */
    },
    exportPdf() {
      let pdfNamed = this.$store.getters.collect.name
      // let pdfAddress = this.$store.getters.pdfDetail.address
      // let pdfphone = this.$store.getters.pdfDetail.phone

      this.customer = { 
        name: pdfNamed, 
        address: this.$store.getters.detail ? this.$store.getters.detail.address : '-', 
        telephone: this.$store.getters.detail ? this.$store.getters.detail.phone : '-',
        pattern: this.$store.getters.get ? this.$store.getters.get.pattern : '-',
        size: this.$store.getters.get ? this.$store.getters.get.size : '-',
        color: this.$store.getters.get ? this.$store.getters.get.color : '-',
        amount: this.$store.getters.get ? this.$store.getters.get.amount: '-'
        
      }
      var arr = Object.values(this.$store.getters.get);
      console.log("dvs");
      // console.log(pdfAddress + " " + pdfphone);
      console.log(arr[0].amount);
      // console.log(arr[0].color);
      let pdfName = "order_list";
      var doc = new jsPDF();
      var imgData = 'data:image/jpeg;base64,';
      // var imgData2 = 'data:image/jpeg;base64,'+ Base64.encode('@/assets/WaterMark.png'); 
      // doc.addImage(imgData2,'png',85,5,40,40);

      var imgData2 = 'data:image/png;base64,';
      doc.addImage(imgData2, 'png', 0, 40, 200, 200);
      doc.addImage(imgData, 'png', 85, 5, 40, 40);
      doc.setFontSize(14);
      var y = 122;
      var x = 10;
      for (var i = 0; i < arr.length; i++) {
        // doc.text(this.name[i].pattern, x, y)
        doc.text(arr[i].pattern, x, y)
        x += 50

        // doc.text(this.name[i].color, x, y)
        doc.text(arr[i].color, x, y)
        x += 55

        // doc.text(this.name[i].size, x, y)
        doc.text(arr[i].size, x, y)
        x += 55

        // doc.text(this.name[i].amount, x, y)
        doc.text(arr[i].amount.toString(), x, y)
        x = 10, y+=10
      }
      
      var z = 110;
      for (var j = 0; j < this.column.length; j++) {
        doc.text(this.column[j].pattern, x, z)
        x += 50

        doc.text(this.column[j].color, x, z)
        x += 55

        doc.text(this.column[j].size, x, z)
        x += 55

        doc.text(this.column[j].amount, x, z)
        x = 10
      }

      var b = 70;
      var e = 40;
      for (var a = 0; a < this.customer.length; a++) {
        doc.text(this.customer[a].name, e, b)
        b += 10

        doc.text(this.customer[a].address, e, b)
        b += 10

        doc.text(this.customer[a].telephone, e, b)
      }

      var c = 70;
      for (var d = 0; d < this.customerH.length; d++) {
        doc.text(this.customerH[d].nameH, x, c)
        doc.text(this.customer.name, x + 30, c)
        c += 10

        doc.text(this.customerH[d].addressH, x, c)
        doc.text(this.customer.address, x + 30, c)
        c += 10

        doc.text(this.customerH[d].telephoneH, x, c)
        doc.text(this.customer.telephone, x + 30, c)
      }

      doc.text(this.by, 120, 250)
      doc.text(this.date, 120, 260)
      doc.text(this.bar, 10, 112)
      doc.text(this.title, 93, 55)
      doc.save(pdfName + '.pdf');
      
       
    }
    
  }
};
</script>

<style>
.blockButton{
  padding: .1rem 1rem;
  width: 100%;
}
.navbar-nav{
  width: 100% !important;;
}
</style>


